.filter__item {
}
.filter__item input {
  position: absolute;
  appearance: none;
}
.filter__list {
  display: flex;
  column-gap: 25px;
  margin-bottom: 45px;
  justify-content: center;
}
.filter__item input ~ span {
  display: inline-block;
  padding: 5px 10px;
  border: 2px solid lch(39.625% 6.711 70deg);
  border-color: lch(68.002% 55.309 70deg);

  border-radius: 5px;
  font-weight: 500;
  cursor: pointer;
  transition: 0.4s;
  /* position: relative;
  display: block;
  width: 15px;
  height: 15px;
  border: 2px solid darkblue; */
}

.filter__item input:checked ~ span {
  background-color: lch(39.625% 6.711 70deg);
  background-color: lch(68.002% 55.309 70deg);

  color: #fff;

