.education {
  padding: 80px 15px;
  background-color: rgb(239, 225, 189);
}
.education__list {
  padding-bottom: 0;
}

.vertical-timeline-element-date {
  color: #000 !important;
}
.education__item span.vertical-timeline-element-icon {
  background-image: url("../icons/education-icon.png") !important;
  background-repeat: no-repeat !important;
  background-position: center !important;

  background-color: #e9d5a1 !important;
}
.education__item:nth-child(2) span.vertical-timeline-element-icon {
  background-image: url("../icons/web-icon3.png") !important;
}
.education__item:nth-child(3) span.vertical-timeline-element-icon {
  background-image: url("../icons//watch-icon.png") !important;
  background-size: 44px !important;
}
.vertical-timeline-element-content {
  background-color: #fff !important;
  color: #000 !important;
  border-radius: 10px;
}
.education__item:nth-child(3) .vertical-timeline-element-content {
  display: none;
}
.vertical-timeline-element-content-arrow {
  border-right: 7px solid #fff !important;
}
.education__item-logo {
  margin-bottom: 15px;
}
