.about-me {
  padding: 60px 0;

  background-color: #fafafa;
  background-image: url("../images/about-bg.jpg");
}
.about-me__container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.about-me__content {
  /* width: 640px;
  display: flex;
  flex-direction: column;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 5px; */
  display: flex;
  column-gap: 25px;
}
.about-me__image img {
  width: 400px;
}
.about-me__text {
  width: 640px;
  display: flex;
  flex-direction: column;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 5px;
}
.about-me__top {
  padding: 7px 12px;
  display: flex;
  column-gap: 7px;
  background-color: rgba(0, 0, 0, 0.03);
  border-bottom: 1px solid rgba(0, 0, 0, 0.125);
}
.about-me__top span {
  width: 10px;
  height: 10px;
  border-radius: 50%;
}
.about-me__top span:first-child {
  background-color: rgb(237, 76, 92);
}
.about-me__top span:nth-child(2) {
  background-color: rgb(253, 203, 88);
}
.about-me__top span:last-child {
  background-color: rgb(120, 177, 89);
}

.about-me__title {
  align-self: center;
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 50px;
}
.about-me__greetings {
  padding: 25px 25px 0 25px;
}
.about-me__info {
  padding: 25px;
  line-height: 170%;
  text-align: justify;
}
